<template>
  <div class="content">
    <div class="head">
      <img src="../../../static/image/icon_back.png" alt @click="back" />
      提交订单
    </div>
    <div class="top" @click="adress">
      <div class="top_img1">
        <img src="../../../static/image/weizhi.png" alt />
      </div>
      <div class="top_address">
        <div class="name">
          <p>{{address.area}}</p>
          <p>{{address.phone}}</p>
        </div>
        <div class="name_boo">
          <p>{{address.province_id}} {{address.city_id}} {{address.district_id}} {{address.addr}}</p>
        </div>
      </div>
    </div>

    <div class="add-address top" @click="adress" v-show= 'bol'>
      <div class="top_img1">
        <img src="../../../static/image/weizhi.png" alt />
      </div>
      <p class>添加收货地址</p>
    </div>
    <div class="bottom_bar">
      <img src="../../../static/image/addres_bar.jpg" />
    </div>
    <div class="main">
      <div class="main_top">
        <img src="../../../static/image/gouwu.png" alt />
        <span>{{goods.goods_desc}}</span>
      </div>

      <div class="main_goods">
        <div class="goods_left">
          <img v-lazy="goods.goods_logo" alt />
        </div>
        <div class="goods_right">
          <div>{{goods.goods_title}}</div>
          <div class="right_price">
            <div class="price1">
              ￥{{market_price}}
            </div>
            <div class="num">X{{num}}</div>
          </div>
        </div>
      </div>
      <div class="main_price">
        <p>商品金额</p>
        <p>￥{{market_price*num}}</p>
      </div>
    </div>
    <div class="boo2">
      <div class="money-left">
        <img src="../../../static/image/zhifu.png" alt />
        <div class="money-group">
          <p>{{jiequ(xianjin)*1}} 现金 + {{jiequ(opf)*1}} OPF</p>
        </div>
      </div>
      <img src="../../../static/image/dui.png" @click="changePayType(1)" v-if="payType==1" alt />
      <img src="../../../static/image/yuan.png" @click="changePayType(1)" v-else alt />
    </div>

    <div class="order">
      <div>
        应付：
        <span>{{xianjin*1}} 现金 + {{opf*1}} OPF</span>
      </div>
      <div class="order1" @click="submit" v-if="subFlag">提交订单</div>
      <div class="order1" v-else>正在提交...</div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data() {
    return {
      selling_price: 0,
      deduction_price: 0,
      xianjin: "",
      opf: "",
      market_price:'',
      subFlag: true,
      id: 0,
      num: 0,
      bol: false,
      goods: "",
      address: "",
      profile: {},
      payType: 1
    };
  },
  created() {
    this.id = this.$route.query.id;
    this.num = this.$route.query.num;
    this.$post("/apiv1/order/address").then(res => {
      console.log(res);
      if (res.code == 0) {
      } else {
        this.address = res.data;
      }
    });
    this.$post("/apiv1/order/goodsdetailed", {
      goods_id: this.id,
      num: this.num
    }).then(res => {
      console.log(res);
      this.goods = res.data;
      this.market_price = Number(res.data.market_price);
      this.xianjin = res.data.xianjin;
      this.opf = res.data.opf;
      console.log(this.goods);
    });
    // this.$post("/user/profile").then(res => {
    //   this.profile = res.data;
    // });
  },
  methods: {
    back() {
      this.$router.back();
    },
    adress() {
      this.$router.push("/shipaddress");
    },
    submit() {
      this.subFlag = false;
      this.$post("/apiv1/order/addorder", {
        addr_id: this.address.id,
        goods_id: this.id,
        num: this.num
      }).then(res => {
        console.log(res);
        this.$msg(res.msg);
        if (res.code == 1) {
          this.$router.push("/dingdan");
        } else {
          this.subFlag = true;
        }
      });
    },
    changePayType(type) {
      this.payType = type;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/scss/mixin";
.content {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  background: #0d1637;
  .head {
    width: 100%;
    height: 110px / $ppr;
    position: relative;
    text-align: center;
    line-height: 110px / $ppr;
    font-size: 36px / $ppr;
    color: #fff;
    background: #0d1637;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    img {
      width: 17px / $ppr;
      height: 29px / $ppr;
      display: block;
      position: absolute;
      top: 40px / $ppr;
      left: 44px / $ppr;
    }
  }
}
.top {
  width: 100%;
  padding:0.8rem 0 1rem 0;
  background: #1b285a;
  color: #fff;
  margin-top: 2.75rem;
  display: flex;
  justify-content: space-around;

  .top_img1 {
    width: 20%;

    img {
      width: 37px / $ppr;
      height: 35px / $ppr;
      margin: 0 auto;
    }
  }

  .top_address {
    width: 80%;
    display: flex;
    flex-direction: column;

    .name {
      display: flex;
      justify-content: space-between;
      font-family: PingFang-SC-Regular;
      font-size: 25px / $ppr;
      font-weight: bold;
      font-stretch: normal;
      line-height: 40px / $ppr;
      letter-spacing: 0px;

      p:last-child {
        padding-right: 60px / $ppr;
      }
    }

    .name_boo {
      font-family: PingFang-SC-Regular;
      font-size: 24px / $ppr;
      font-weight: bold;
      font-stretch: normal;
      line-height: 40px / $ppr;
      letter-spacing: 1px / $ppr;
      color: #979797;
      padding-top: 10px / $ppr;
    }
  }
}

.add-address {
  align-items: center;
  justify-content: center;

  .top_img1 {
    margin: 0;
  }
}

.main {
  width: 100%;
  background: #1b285a;
  color: #fff;
  margin-top: 20px / $ppr;

  .main_top {
    height: 90px / $ppr;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #e6e6e6;

    img {
      width: 30px / $ppr;
      height: 30px / $ppr;
      margin-left: 20px / $ppr;
      margin-right: 15px / $ppr;
    }

    span {
      font-family: PingFang-SC-Regular;
      font-size: 26px / $ppr;
      font-weight: bold;
      font-stretch: normal;
      letter-spacing: 0px;
    }
  }

  .main_goods {
    height: 246px / $ppr;
    display: flex;
    justify-content: space-around;
    border-bottom: 1px / $ppr solid #e6e6e6;

    .goods_left {
      width: 30%;

      img {
        width: 200px / $ppr;
        height: 200px / $ppr;
        margin: 30px / $ppr 0 0 20px / $ppr;
      }
    }

    .goods_right {
      width: 497px / $ppr;
      height: 65px / $ppr;
      font-family: PingFang-SC-Regular;
      font-size: 26px / $ppr;
      font-weight: bold;
      font-stretch: normal;
      line-height: 40px / $ppr;
      letter-spacing: 0px;
      margin-top: 47px / $ppr;

      .right_price {
        display: flex;
        justify-content: space-between;
        margin-top: 40px / $ppr;

        .price1 {
          font-family: PingFang-SC-Regular;
          font-size: 28px / $ppr;
          font-weight: bold;
          letter-spacing: 0px;
          color: #7386f5;

          span {
            font-family: PingFang-SC-Regular;
            font-size: 24px / $ppr;
            font-weight: normal;
            letter-spacing: 0px;
            color: #979797;
            text-decoration: line-through;
          }
        }

        .num {
          padding-right: 100px / $ppr;
        }
      }
    }
  }

  .main_price {
    height: 108px / $ppr;
    display: flex;
    justify-content: space-between;
    font-family: PingFang-SC-Medium;
    font-size: 28px / $ppr;
    font-weight: bold;
    font-stretch: normal;
    line-height: 108px / $ppr;
    letter-spacing: -1px / $ppr;
    border-bottom: 1px / $ppr solid #e6e6e6;

    p:first-child {
      padding-left: 30px / $ppr;
    }

    p:last-child {
      color: #7386f5;
      padding-right: 30px / $ppr;
    }
  }

  .main_boo {
    height: 108px / $ppr;
    display: flex;
    justify-content: space-between;
    font-family: PingFang-SC-Medium;
    font-size: 28px / $ppr;
    font-weight: bold;
    font-stretch: normal;
    line-height: 108px / $ppr;
    letter-spacing: -1px / $ppr;

    p:first-child {
      padding-left: 30px / $ppr;
    }

    p:last-child {
      color: #2a2a2a;
      padding-right: 30px / $ppr;
    }
  }
}

.boo1 {
  height: 110px / $ppr;
  display: flex;
  justify-content: space-between;
  margin-top: 30px / $ppr;
  background: #1b285a;
  color: #fff;

  img {
    width: 38px / $ppr;
    height: 28px / $ppr;
    margin-top: 37px / $ppr;
    margin-left: 20px / $ppr;
  }

  img:last-child {
    margin-right: 20px / $ppr;
    width: 38px / $ppr;
    height: 38px / $ppr;
  }

  p {
    font-family: PingFang-SC-Medium;
    font-size: 28px / $ppr;
    font-weight: bold;
    font-stretch: normal;
    line-height: 108px / $ppr;
    letter-spacing: -1px / $ppr;
    padding-right: 50px / $ppr;
  }
}

.boo2 {
  height: 129px / $ppr;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 30px / $ppr;
  background: #1b285a;

  .money-left {
    align-items: center;
    display: flex;
  }

  img {
    width: 38px / $ppr;
    height: 42px / $ppr;
    margin-left: 35px / $ppr;
    margin-right: 30px / $ppr;
  }

  img:last-child {
    margin-right: 20px / $ppr;
    width: 38px / $ppr;
    height: 38px / $ppr;
  }

  p {
    font-family: PingFang-SC-Medium;
    font-size: 28px / $ppr;
    font-weight: bold;
    font-stretch: normal;
    letter-spacing: -1px / $ppr;
    color: #505050;
    padding-right: 50px / $ppr;
  }

  p:last-child {
    font-family: PingFang-SC-Medium;
    font-size: 24px / $ppr;
    font-weight: bold;
    color: #aeaeae;
  }
}

.order {
  width: 100%;
  height: 97px / $ppr;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  background: #1b285a;

  div {
    margin-left: 30px / $ppr;
    font-family: PingFang-SC-Medium;
    font-size: 28px / $ppr;
    font-weight: bold;
    font-stretch: normal;
    line-height: 97px / $ppr;
    letter-spacing: -1px / $ppr;
    color: #fff;

    span {
      color: #7386f5;
    }
  }

  .order1 {
    float: right;
    width: 211px / $ppr;
    height: 97px / $ppr;
    background: linear-gradient(-90deg, #4a64a1 0%, #413483 100%);
    font-family: PingFang-SC-Medium;
    font-size: 28px / $ppr;
    font-weight: bold;
    font-stretch: normal;
    line-height: 97px / $ppr;
    letter-spacing: -1px / $ppr;
    color: #fff;
    text-align: center;
  }
}
</style>



